<template>
  <div class="section__business-fields">
    <div class="field field--show-floating-label">
      <div class="checkbox-wrapper">
        <div class="checkbox__input">
          <input
            id="business_toggle"
            v-model="showBusinessFields"
            class="input-checkbox is-active"
            type="checkbox"
          >
        </div>
        <label
          class="checkbox__label"
          for="business_toggle"
          v-text="translations.businessCustomer"
        />
      </div>
    </div>
    <transition
      name="expand"
      @enter="enter"
    >
      <div
        v-if="showBusinessFields"
        ref="inputsContainer"
        class="section__business-fields-inputs-container"
        :style="{
          height: inputsHeight
        }"
      >
        <div
          ref="inputs"
          class="section__business-fields-inputs"
        >
          <div
            class="field field--required field--half"
            data-address-field="first_name"
          >
            <div class="field__input-wrapper">
              <label
                class="field__label field__label--visible"
                for="checkout_shipping_address_business_name"
                v-text="translations.businessName"
              />
              <input
                id="checkout_shipping_address_business_name"
                v-model="business_name"
                :placeholder="translations.businessName"
                autocorrect="off"
                class="field__input"
                aria-required="true"
                type="text"
                name="checkout[attributes][business_name]"
                :maxlength="customerMaxChars"
              >
              <div class="business-field__note">
                <span
                  v-text="customerMaxChars - business_name.length"
                />&nbsp;<span
                  v-text="translations.businessMaxCharacterSuffix"
                />
              </div>
            </div>
          </div>
          <div
            class="field field--required field--half"
            data-address-field="first_name"
          >
            <div class="field__input-wrapper">
              <label
                class="field__label field__label--visible"
                for="checkout_shipping_address_abn_number"
                v-text="translations.businessAbn"
              />
              <input
                id="checkout_shipping_address_abn_number"
                v-model="business_abn"
                :placeholder="translations.businessAbn"
                autocorrect="off"
                class="field__input"
                aria-required="true"
                type="text"
                :name="`checkout[attributes][${abnFieldName}]`"
                :maxlength="abnMaxChars"
              >
              <div class="business-field__note">
                <span
                  v-text="abnMaxChars - business_abn.length"
                />&nbsp;<span
                  v-text="translations.businessMaxCharacterSuffix"
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>

 /**
 * Vue: Checkout business fields (checkout-business-fields)
 * -----------------------------------------------------------------------------
 *
 */
import { debounce } from '~/helpers/general'

export default {
  name: 'CheckoutBusinessFields',
  props: {
    customerMaxChars: {
      type: Number,
      default: 35,
    },
    abnMaxChars: {
      type: Number,
      default: 11,
    },
    abnFieldName: {
      type: String,
      default: 'abn',
    },
  },
  data() {
    const { checkout } = window.cnvs.strings
    return {
      showBusinessFields: false,
      business_name: '',
      business_abn: '',
      inputsHeight: 0,
      translations: {
        ...checkout,
      },
    }
  },
  mounted() {
    window.addEventListener(
      'resize',
      debounce(() => this.handleResize(), 150),
    )
  },
  methods: {
    enter() {
      this.setInputsHeight()
    },
    setInputsHeight() {
      this.inputsHeight = `${this.$refs.inputs.getBoundingClientRect().height}px`
    },
    handleResize() {
      if (!this.showBusinessFields) {
        return
      }

      this.setInputsHeight()
    },
  },
}
</script>